.header {
    position: sticky;
    top: 0;
    display: flex;
    background-color: white;
    justify-content: space-evenly;
    border-bottom: 0.1px solid lightgrey;
    padding-top: 10px;
    padding-bottom: 10px;
    width: 100%;
    z-index: 1;
}

.header_left {
    display: flex;
    align-items: center;
    justify-content: center;
}

.header_left>img {
    object-fit: contain;
    height: 40px;
    margin-right: 10px;
}

.header_search {
    padding: 10px;
    display: flex;
    align-items: center;
    border-radius: 5px;
    height: 40px;
    color: black;
    background-color: #eef3f8;
}

.header_search>input {
    outline: none;
    border: none;
    background: none;
}

.header_right {
    display: flex;
}

@media screen and (max-width:800px) {
    .header_search>input {
        width: 90px;
    }
}